t, rgba(26, 26, 26, 0.5), rgba(26, 26, 26, 0.8), hsl(0deg, 0%, 10%))}.carousel section button.next-btn svg{transform:rotate(180deg)}.carousel section button svg{width:3rem;height:3rem;fill:#e10510}.carousel section ul{padding-left:0;list-style:none;display:flex;column-gap:2vw;overflow-x:hidden;scroll-behavior:smooth;will-change:scroll-position}@media screen and (max-width: 660px){.carousel section ul{overflow-x:scroll}.carousel section ul::-webkit-scrollbar{display:none}}.carousel section ul li{width:25vw;white-space:nowrap}@media screen and (max-width: 660px){.carousel section ul li{width:40vw}}.carousel section ul li a{text-decoration:none;color:#fff;display:flex;flex-direction:column;row-gap:.5rem;align-items:center;overflow:hidden;text-overflow:ellipsis}.carousel section ul li a img{width:25vw}@media screen and (max-width: 660px){.carousel section ul li a img{width:40vw}}.banner{font-size:1.4rem;padding-left:2rem;padding-right:2rem;display:flex;flex-direction:column;align-items:center;justify-content:center;position:relative;top:-6rem}@media screen and (max-width: 660px){.banner{padding-left:1rem;padding-right:1rem;font-size:1rem}}.banner p{margin-top:0;width:60%;text-align:center;text-align:center}@media screen and (max-width: 660px){.banner p{width:90%}}.banner button{font-size:inherit;padding:.5rem 2rem}@media screen and (max-width: 660px){.banner button{width:90%}}